.container {
  position: fixed;
  background-color: transparent;
  transition: background-color 0.3s ease;
  width: 100%;
  padding-top: 20px;
  top: 0;
  z-index: 1;
}

.container-bg {
  background-color: #333;
  transition: background-color 0.3s ease;
  width: 100%;
  height: 60px;
  background: #131212;
  position: fixed;
  z-index: 1;
  padding-bottom: 20px;
}

.button {
  background-color: #000000;
  color: #ffffff;
  border: none;
  padding: 10px 20px;
  margin: 0 10px;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.button:hover {
  background-color: #1558b0;
  transform: translateY(-2px);
}

#nav {
  font-weight: bold;
}

.button:active {
  transform: translateY(1px);
}

@media (max-width: 600px) {
  .button {
    font-size: 14px;
    padding: 8px 16px;
    margin: 0 5px;
  }
}
